import 'ol/ol.css'; //
import {Map as OlMap, View} from 'ol'; // 
import {fromLonLat, get as getProjection} from 'ol/proj'; //
import {Tile as TileLayer} from 'ol/layer'; //
import XYZ from 'ol/source/XYZ';
import {useEffect, useState} from "react";

function OpenLayersMap({server}) {
    const [mapObject, setMapObject] = useState({})
    useEffect(() => {
        const map = new OlMap({
            layers: [
                new TileLayer({
                    // source: new OSM()
                    source: new XYZ({
                        url: `${server}/xyz/{z}/{x}/{y}.png`
                    })
                })
            ],
            target: 'map',
            view: new View({
                // projection: getProjection('EPSG:3857'),
                center: fromLonLat([0, 0], getProjection('EPSG:3857')),
                zoom: 1,
                // disable horizontal infinity scroll
                // extent: [-20037508.34, -20037508.34, 20037508.34, 20037508.34],
                // maxZoom: 19,
                // minZoom: 0
            })
        })
        setMapObject({map})
        return () => null
    }, [server]);
    return <div id="map" value={mapObject} style={{height: '1000px', width: '1000px', margin: "auto"}}/>
}

export default OpenLayersMap;
